import Logo from "../assets/studig.png";
import swal from "sweetalert";
import { mapState } from "vuex";
import { USER_ERROR } from "../store/types/userTypes";

export default {
  name: "UserForm",
  data: () => ({
    logo: Logo,
    typeForm: "",
    name: "",
    email: "",
    formSubmit: "",
  }),
  computed: {
    ...mapState("userStore", ["userError", "userLoading"]),
  },
  beforeCreate() {
    if (this.$route.name == "register") {
      if (localStorage.getItem("user_sg")) {
        this.$router.push("/");
      }
    }
  },
  created() {
    if (this.$route.name == "user") {
      this.typeForm = "user";
      const user =
        localStorage.getItem("user_sg") &&
        JSON.parse(localStorage.getItem("user_sg"));

      this.name = user.name;
      this.email = user.email;
      this.formSubmit = this.editUser;
    }
    if (this.$route.name == "register") {
      this.typeForm = "register";
      this.formSubmit = this.newUser;
    }
  },
  methods: {
    editUser: async function (ev) {
      ev.preventDefault();
      const form = new FormData();
      const name = ev.srcElement[0].value;
      const email = ev.srcElement[1].value;
      const pass = ev.srcElement[2].value;
      const repetPass = ev.srcElement[3].value;

      if (pass != "") {
        if (pass != repetPass) {
          return swal({
            title: "Error!",
            text: "error password",
            icon: "warning",
            button: "Close!",
          });
        }
        form.append("pwss", pass);
      }

      form.append("name", name);
      form.append("email", email);

      const formValues = {
        form,
        type: "PUT",
      };

      this.$store
        .dispatch("userStore/userCreate", formValues)
        .then((result) => {
          if (result) {
            this.name = name;
            this.email = email;
          }
        });
    },
    newUser: async function (ev) {
      ev.preventDefault();

      const form = new FormData();

      try {
        const name = ev.srcElement[0].value;
        const email = ev.srcElement[1].value;
        const pass = ev.srcElement[2].value;
        const repetPass = ev.srcElement[3].value;

        if (pass != repetPass) {
          return swal({
            title: "Error!",
            text: "the passwords do not match",
            icon: "warning",
            button: "Close!",
          });
        }

        form.append("name", name);
        form.append("email", email);
        form.append("pwss", pass);
        const formValues = {
          form,
          type: "POST",
        };
        const result = await this.$store.dispatch(
          "userStore/userCreate",
          formValues
        );

        if (result) {
          swal({
            title: "Success!",
            text: result,
            icon: "success",
            button: "Close!",
          }).then(() => {
            this.$router.push("/login");
          });
        } else {
          ev.target.querySelectorAll("input")[2].value = "";
          ev.target.querySelectorAll("input")[3].value = "";
        }
      } catch (error) {
        return false;
      }
    },
    handleChangeValue: function (ev) {
      console.log({
        ev: ev.target.value,
      });
    },
    clearError: function () {
      this.$store.commit(`userStore/${USER_ERROR}`, "");
    },
  },
  watch: {
    userError() {
      if (this.userError != "") {
        swal({
          title: "Error!",
          text: this.userError,
          icon: "warning",
          button: "Close!",
        }).then(() => {
          this.$store.commit(`userStore/${USER_ERROR}`, "");
        });
      }
    },
  },
  destroyed() {
    this.$store.commit(`userStore/${USER_ERROR}`, "");
  },
};
